.result{width:72%;margin:0 auto;text-align:center}@media screen and (max-width:480px){.result{width:100%}}.result .icon{margin-bottom:24px;font-size:72px;line-height:72px}.result .icon>.success{color:#52c41a}.result .icon>.error{color:#f5222d}.result .title{margin-bottom:16px;color:rgba(0,0,0,.85);font-weight:500;font-size:24px;line-height:32px}.result .description{margin-bottom:24px;color:rgba(0,0,0,.45);font-size:14px;line-height:22px}.result .extra{padding:24px 40px;text-align:left;background:#fafafa;border-radius:2px}@media screen and (max-width:480px){.result .extra{padding:18px 20px}}.result .actions{margin-top:32px}.result .actions button:not(:last-child){margin-right:8px}